body,img,ul,li,a,p,h1,h2,h3,h4,h5,h6{
  padding:0;
  margin:0;
  text-decoration: none;
  border:0;
  font-family:Lato;
}

@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?892rdc');
  src:  url('../fonts/icomoon.eot?892rdc#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?892rdc') format('truetype'),
    url('../fonts/icomoon.woff?892rdc') format('woff'),
    url('../fonts/icomoon.svg?892rdc#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

.nav-con{
  width:100%;
  height:60px;
  background-color:rgba(0, 0, 0, 0.8);;
  position:fixed;
  z-index:9999;
  top:0;
  .nav-bar{
    max-width: 940px;
    margin: 0 auto;
    padding: 0px 20px;
    img{
      width:40px;
    }
    ul{
      display:inline-block;
      li{
        display:inline-block;
        margin-right:20px;
        &:nth-of-type(2) a:before{
          background: #40b3b6;
        }
        &:nth-of-type(3) a:before{
          background:#e2306c
        }
        &:nth-of-type(4) a:before{
          background:#1e7bb4;
        }
        a{
          color: #fff;
          position: relative;
          line-height: 60px;
          font-weight: 300;
          letter-spacing: 1px;
          font-size: 14px;
          text-transform: uppercase;
          &:before{
            content: "";
            position: absolute;
            left: 0;
            bottom: -3px;
            width: 100%;
            height: 3px;
            background: #f8d440;
            transform: scale3d(0, 1, 1);
            transition: transform 0.1s;
          }
          &:hover:before{
            transform: scale3d(1, 1, 1);
            transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
            transition-duration: 0.3s;
          }
        }
      }
    }
    .social{
      display: inline-block;
      float: right;
      color: #fff;
      font-size: 20px;
      line-height: 60px;
      ul{
        li{
          margin-right:0;
          margin-left:20px;
          font-family:icomoon;
          height: 42px;
          width: 20px;
          position: relative;
          a{
            position: absolute;
            display: block;
            width: 100%;
            height: 100%;
            left: 0;
            top:0;
          }
        }
        .insta:before {
          content: "\ea92";
        }
        .github:before {
          content: "\eab0";
        }
        .linked-in:before {
          content: "\eaca";
        }
      }
    }
  }
}
